.styledTable {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);

  th,
  td {
    padding: 12px 15px;
  }

  tbody tr {
    border-bottom: 1px solid #dddddd;
  }

  tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
  }

  tbody tr:last-of-type {
    border-bottom: 2px solid #009879;
  }

  tbody tr.active-row {
    font-weight: bold;
    color: #009879;
  }
}

.left {
  padding: 10px;
  li {
    list-style-type: none;
    padding: 10px;
    padding-right: 10px;
    border-right: 1.8px solid #c9a66b;
  }
  button {
    width: 80px;
    background-color: #ebdcb2;
    color: #662e1c;
    border: 1.8px solid #c9a66b;
    border-radius: 5px;
  }

  .none {
    text-decoration: none;
    color: #c9a66b;
  }
}
